/* App.css */

/* Reset default browser styles */
body, html {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
}

/* Center the content */
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #000; /* Change background color to black */
    color: #6a0dad; /* Change font color to purple */
}

/* Container for the To-do app */
.todo-app {
    width: 1000px; /* Increased width */
    padding: 40px; /* Increased padding */
    background-color: #000000;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center; /* Center align text */
    position: relative; /* Set position to relative */
}

.todo-title {
    font-size: 56px; /* Increased font size */
    font-weight: bold; /* Added font weight for header */
    color: #6a0dad;
    margin-bottom: 20px; /* Added margin bottom for spacing */
}

/* Input field */
.todo-input {
    width: 270px;
    padding: 15px; /* Increased padding */
    margin-bottom: 20px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: 20px; /* Increased font size */
}

/* Add task button */
.todo-btn {
    width: 122px;
    padding: 16px;
    background-color: #800080; /* Change button background color to purple */
    color: #fff; /* Change button text color to white */
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 20px; /* Increased font size */
}

.todo-btn:hover {
    background-color: #6a0dad; /* Darken button background color on hover */
}

/* Task list */
.task-item-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px; /* Adjust spacing between task items */
}

.task-item {
    width: 440px; /* Set width to 115px */
    height: 30px;
    background-color: #fff; /* White box background */
    color: #000; /* Black font color */
    padding: 10px;
    margin-right: 10px; /* Adjust spacing between task content and actions */
    display: flex; /* Ensures items align horizontally */
    align-items: center; /* Center items vertically */
}


.task-item.completed {
    text-decoration: line-through; /* Add strike-through for completed tasks */
}

.task-actions button {
    height: 51px;;
    border: none;
    background: purple;
    font-size: small;
    color: white;
    cursor: pointer;
    margin-left: 2px;
    width: 74px;
}

.task-actions button svg {
    width: 20px; /* Adjust icon size */
    height: 20px; /* Adjust icon size */
}

/* Style for the recycle bin icon */
.task-actions .delete-btn svg {
    fill: red; /* Red color for the recycle bin */
}

/* Style for the tick icon */
.task-actions .complete-btn svg {
    fill: green; /* Green color for the tick */
}

/* Additional styles for task action buttons */
.delete-btn {
    color: red;
}

.complete-btn {
    color: green;
}
